<template>
  <div>
    <h2>User Details</h2>

    <!-- Search Bar -->
    <input v-model="searchQuery" placeholder="Search users" @input="searchUsers" />

    <!-- User Table -->
    <table>
      <thead>
        <tr>
          <th>Username</th>
          <th>Email</th>
          <th>Phone</th>
          <th>ID</th>
          <th>Creation Date</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in filteredUsers" :key="user.id" @click="openUserModal(user)">
          <td>{{ user.username }}</td>
          <td>{{ user.email }}</td>
          <td>{{ user.phone }}</td>
          <td>{{ user.id }}</td>
          <td>{{ user.creationDate }}</td>
        </tr>
      </tbody>
    </table>

    <!-- User Modal -->
    <div v-if="selectedUser">
      <h3>User Report</h3>
      <p>Username: {{ selectedUser.username }}</p>
      <p>Email: {{ selectedUser.email }}</p>
      <p>Phone: {{ selectedUser.phone }}</p>
      <!-- Add other user details as needed -->
      <button @click="generateReport">Generate Report</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      users: [], // Your user data fetched from the database
      searchQuery: "",
      selectedUser: null,
    };
  },
  computed: {
    filteredUsers() {
      return this.users.filter(user =>
        user.username.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
  },
  methods: {
    searchUsers() {
      // Implement search functionality
    },
    openUserModal(user) {
      this.selectedUser = user;
    },
    generateReport() {
      // Implement report generation logic
    },
  },
};
</script>

